nav.header-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  background: #333;
}
h6, p {
  margin: 0;
}
ul {
  list-style: none;
  padding: 0;
}
.links-wrapper ul {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
}
.links-wrapper ul li a {
  margin: 0 15px;
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
}
.products {
  color: #FFFFFF;
}
.list-wrapper img {
  width:  350px;
  height: 300px;
  object-fit: cover;
}
.shopping-list-row {
  width: clamp(0px, 100%, 1280px);
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 60px 80px;
  /* column-gap: 60px; */
  justify-content: center;
  align-items: center;
}
.shopping-page-title, h1 {
  text-align: center;
}
.product-title,
.product-price {
  text-align: center;
  font-size: 20px;
}
.add-to-cart-btn {
  background: #313131;
  border: 2px solid #313131;
  border-radius: 25px;
  color: #FFFFFF;
  padding: 10px 15px;
  text-align: center;
  cursor: pointer;
  margin-top: 20px;
  transition: 0.5s all;
  font-size: 18px;
  font-weight: 600;
  outline: 0;
}
.add-to-cart-btn:hover {
  background: #424242;
  border: 2px solid #424242;
  transition: 0.5s all;
}
.list-wrapper {
  text-align: center;
}
.cart-img-wrapper {
  width: 200px;
  height: 200px;
  padding: 15px;
}
.cart-img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.cart-wrapper,
.cart-total-wrapper {
  width: clamp(0px, 100%, 1060px);
  margin: 0 auto;
}
.cart-total-wrapper {
  text-align: center;
  padding: 60px 0px;
}
.cart-wrapper {
  display:flex;
  flex-wrap: wrap;
  gap: 60px 80px;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.cart-decrement,
.cart-increment {
  background: #313131;
  border: 2px solid #313131;
  color: white;
  text-align: center;
  padding: 5px 10px;
  margin-top: 10px;
  margin-left: 5px;
  margin-right: 5px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.5s all;
}
.cart-field {
  background: white;
  width: 45px;
  text-align: center;
  padding: 5px;
  margin-top: 10px;
}
a.go-to-cart {
  background: #B31312;
  border: 2px solid #B31312;
  border-radius: 25px;
  color: #FFFFFF;
  padding: 10px 25px;
  text-align: center;
  cursor: pointer;
  margin-top: 20px;
  margin-left: 20px;
  text-decoration: none;
  transition: 0.5s all;
  font-size: 18px;
  font-weight: 600;
  outline: 0;
}
a.go-to-cart:hover {
  background: transparent; 
  color: #B31312;
  border: 2px solid #B31312;
  transition: 0.5s all;
}
.delete-cart {
  background: #D71313;
  border: 2px solid #D71313;
  color: #fff;
  text-align: center;
  border-radius: 25px;
  cursor: pointer;
  padding: 10px 25px;
  margin-top: 20px;
  text-decoration: none;
  transition: 0.5s all;
  font-size: 18px;
  font-weight: 600;
  outline: 0;
}
.delete-cart:hover {
  background: transparent;
  color: #D71313;
  border: 2px solid #D71313;
  transition: 0.5s all;
}
