.filter-wrapper-sidebar .filter-heading {
  font-size: 20px;
  line-height: normal;
}
.cart-wrapper, .cart-total-wrapper {
  width: clamp(0px, 100%, 550px);
}

.cart-wrapper {
  gap: 40px 40px;
}

.cart-item-list-wrapper {
  display: flex;
  flex-wrap: nowrap;
  flex: 0 0 100%;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border-radius: 10px;
}

.cart-item-list {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
  column-gap: 20px;
}
.cart-to-shopping-bttn {
  background: #323232;
  border: 2px solid #323232;
  padding: 15px 25px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  line-height: normal;
  border-radius: 15px;
  cursor: pointer;
  transition: 0.5s all;
  margin-top: 30px;
}
.cart-to-shopping-bttn:hover {
  border: 2px solid #373737;
  color: #373737;
  background: transparent;
}
.filter-wrapper-sidebar {
  width: clamp(0px, 100%, 1280px);
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.filter-wrapper-sidebar h6 {
  flex: 0 0 100%;
}
.filter-wrapper-sidebar > div {
  flex: 0 0 33%;
}
